.logo-collapsed-false {
  width: 120px;
  height: 31px;
  background: url(./mtk_logo.png);
  background-size : 120px 24px;
  background-repeat: no-repeat;
  margin: 16px 28px 16px 15px;
  float: left;
}

.logo-collapsed-true{
  width: 20px;
  height: 20px;
  background: url(./logo.png);
  background-size : 20px 20px;
  background-repeat: no-repeat;
  margin: 16px 28px 16px 29px;
  float: left;
  overflow: hidden;
}

.content-border{
  padding: 24px;
  background: #ffffff; 
  min-height: 360px;
}